.header {
	left: 0;
	top: 0;
	padding: 24px 0;
	width: 100%;
	background-color: #87509c;
	padding-top: 50px;
	padding-bottom: 30px;

}

.header-container {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.logo {
	padding-left: 40px;
	transform: translateY(-10px);
}

.nav__link {
	display: block;
	height: 100%;
	font: var(--middle-high-font);
	text-decoration: none;
	color: #f7f3ea;
	transition: all 0.3s ease-in-out;
	text-transform: uppercase;
	text-align: center;
	padding: 4px;
}

.nav {
	padding-right: 60px;
}

.nav__link:hover {
	background-color: #643a79;
	border-radius: 5px;
}


.nav__list {
	display: flex;
	justify-content: space-between;
	margin: 0;
}

.nav__item:not(:last-child) {
	margin-right: 30px;
}

.burger {
	width: 40px;
	height: 20px;
	cursor: pointer;
	background-color: transparent;
	border: none;
	position: relative;
	display: none;
}

.burger::before,
.burger::after {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 2px;
	background-color: #fff;
}

.burger::after {
	top: auto;
	bottom: 0;
}

.burger span {
	position: absolute;
	left: 0;
	top: 50%;
	transform: translateY(-50%);
	width: 100%;
	height: 2px;
	display: inline-block;
	background-color: #fff;
}

.overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0, 0.5);
	z-index: 99;
	opacity: 0;
	visibility: hidden;
}

.lock {
	overflow: hidden;
}

@media (max-width: 768px) {
	.logo {
		padding-top: 20px;
		padding-left: 0;
	}
	.burger {
		display: block;
	}


	.header {
		padding: 15px 0;
	}

	.nav {
		position: fixed;
		left: 0;
		top: 0;
		max-width: 400px;
		width: 80%;
		height: 100vh;
		z-index: 100;
		box-shadow: 5px 0 15px -5px rgba(0,0,0, 0.8);
		background-color: #87509c;
		transform: translateX(-150vw);
		overflow-x: hidden;
		overflow-y: auto;
	}

	.nav__list {
		flex-direction: column;
		align-items: flex-start;
		padding: 0;
		padding-top: 10%;
		position: relative;
	}

	.nav__item {
		margin-left: 0 !important;
		font-size: 20px;
		width: 100%;
		padding: 0;
		position: static;
	}

	.nav__link
	{
		padding: 20px 30px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 24px;
	}

	.nav__link::after {
		transform: rotate(-90deg);
	}
	.nav__list.transformation {
		transform: translateX(-100%);
		transition: all 0.3s;
	}
	.nav.open {
		transform: translateX(0);
		transition: all 0.3s;
	}

	.overlay.open {
		opacity: 1;
		visibility: visible;
		transition: all 0.3s;
	}
}