.contact {
	background-color: #3c5499;
	padding-top: 80px;
	padding-bottom: 60px;
}

.contact-title-line {
	background-color: #273a71;
}

.contact-title, .contact-text {
	color: #fff;
}

.contact-text {
	max-width: 750px;
}

.phone {
	text-decoration: none;
	color: #fff;
}

.form-input {
	border-radius: 20px;
	background: #273a71;
	padding-left: 30px;
	height: 50px;
	font:var(--middle-font);
	line-height: 23px;
	color: #fff;
	border: none;
}

.form-container {
	display: flex;
	justify-content: center;
	width: 100%;
	margin-bottom: 10px;
	@media (max-width: 550px) {
		flex-direction: column;
	}
}

.form-input__content {
	display: inline-block;
	width: 100%;
	
}

.form-input__content:not(:last-child)
{
	margin-right: 10px;
	@media (max-width: 550px) {
		margin-right: 0;
		margin-bottom: 10px;
	}
}

.callback-form__area {
	display: block;
	height: 200px;
	width: 100%;
	padding-top: 20px;
	resize: none;
}

.form-container__area {
	width: 60%;
	margin: 0 auto;
	padding-bottom: 30px;
}

.contact-btn {
	background-color: #30bae7;
	box-shadow: 0px 5px 0px 0px #2282a1;
}

input::placeholder, textarea::placeholder {
	color: rgb(164, 198, 250);
}

input:focus, textarea:focus {
	outline: none;
}