.team {
	background-color: #cde9fd;
	padding-top: 80px;
	padding-bottom: 60px;
}

.team-title {
	color: #434e67
}

.team-text {
	color: #434e67
}

.team-title-line {
	background-color: #b7e0ff;
}

.team-subtitle {
	color: #434e67
}

.team-descr {
	color: #434e67
}

.team-position {
	text-align: center;
	margin-bottom: 20px;
	color: #00bfff;
	font: var(--middle-font);
	font-size: 20px;
}

.social {
	padding-top: 20px;
	display: flex;
	align-items: center;
	margin: 0 auto;
}

.social-item:not(:last-child) {
	margin-right: 11px;
}

.social-link {
	display: block;
	border-radius: 100%;
	width: 33px;
	height: 33px;
	background-color: #bdd1df;
	background-position: center;
	background-repeat: no-repeat;
	background-size: 18px auto;
	opacity: 0.4;
	transition: opacity 0.3s ease-in-out;
}

.social-link:hover {
	opacity: 1;
}

.social-logo {
	width: 20px;
	height: 20px;
	position: relative;
	left: 50%;
	transform: translate(-50%, 40%);
}