html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Titillium Web", sans-serif;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1100px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.general-btn {
	width: 293px;
	height: 60px;
	border-radius: 5px;
	text-transform: uppercase;
	color: #f7f3ea;
	position: relative;
	left: 50%;
	transform: translate(-50%, 0);
	font:var(--middle-font);
}
.general-btn:hover {
	transition: ease-in-out 0.3s;
	opacity: 0.8;
}

.general-text {
	color: #f7f3ea;
	font: var(--middle-high-font);
	max-width: 550px;
	text-align: center;
	margin: 0 auto;
	padding-top: 30px;
	padding-bottom: 80px;
}

.general-subtitle {
	margin: 0;
	text-transform: uppercase;
	color: #f7f3ea;
	text-align: center;
	margin-bottom: 30px;
	margin-top: auto;
  font: var(--middle-high-font);
}

.general-descr {
	color: #f7f3ea;
	max-width: 70%;
	margin: 0 auto;
	text-align: center;
  font: var(--middle-font);
}