.about {
	background-color: #d74680;
	padding-top: 80px;
	padding-bottom: 60px;
}

.about-title-line {
	background-color: #b73c6d;
}

.about-title, .about-text, .about-cite, .about-subtitle, .about-name {
	color: #fff;
}

.about-cite {
	margin: 0;
	font:var(--small-font);
}

.about-name {
	margin-bottom: 3px;
	font:var(--middle-high-font);
}

.about-position {
	color: #ffdd99;
	margin: 0;
	font:var(--middle-font);
}

.about-list {
	display: grid;
	grid-template-columns: repeat(2, 3fr);
	gap: 70px;
	padding-bottom: 40px;

	@include mobile {
		grid-template-columns: repeat(1, 3fr);
	}
}

.about-img {
	width: 90px;
	height: 90px;
	margin-left: 20px;
	margin-right: 20px;
}

.about-item {
	display: flex;
	justify-content: space-between;
}

.about-text-content {
	max-width: 350px;
	line-height: 30px;
}