.footer {
	padding-top: 40px;
	padding-bottom: 60px;
	background-color: #344b8e;
	height: auto;
	font:var(--middle-high-font);
}

.footer-list {
	margin: 0;
	display: flex;
	justify-content: space-between;
	text-transform: uppercase;

	@include mobile {
		flex-direction: column;
		align-items: center;
	}


}

.footer-item:not(:last-child) {
	@include mobile {
		padding-bottom: 20px;
	}
}

.footer-link {
	text-decoration: none;
	color: #fff;
	transition: all 0.3s;
}

.footer-link:hover {
	color: #30b3e1;
	}