.portfolio {
	background-color: #ffdd99;
	padding-top: 80px;
	padding-bottom: 60px;
}

.portfolio-title {
	color: #393939;
}

.portfolio-text {
	color: #393939;
	font-weight: 600;
	max-width: 550px;
	text-align: center;
	margin: 0 auto;
	padding-top: 30px;
	padding-bottom: 30px;
}

.portfolio-subtitle {
	color: #393939;
	margin: 0;
	text-align: center;
	padding-top: 30px;
	font:var(--middle-font);
}

.portfolio-title-line {
	background-color: #e5c37f;
}

.portfolio-category {
	display: flex;
	justify-content: center;
	padding-bottom: 30px;
}
.btn-category {
	background-color: #e5c37f;
	text-transform: uppercase;
	width: 70px;
	height: 30px;
	border-radius: 4px;
	transition: all 0.3s;
	font:var(--middle-font);
}

.btn-category:not(:last-child) {
	margin-right: 15px;
}

.btn-category:hover {
background-color: #fff;
}


.portfolio-img {
	width: 500px;
	height: auto;
}

.portfolio-list {
	display: grid;
	grid-template-columns: repeat(2, 3fr);
	gap: 30px;
	padding-bottom: 40px;

	@include mobile {
		grid-template-columns: repeat(1, 3fr);
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.portfolio-btn {
	background-color: #17c2a4;
	box-shadow: 0px 5px 0px 0px #14a58c;
}