.services {
	background-color: #17c2a4;
	padding-top: 80px;
	padding-bottom: 60px;
}

.general-title {
	margin: 0 auto;
	text-align: center;
	padding-bottom: 15px;
	text-transform: uppercase;
	font: var(--middle-high-font);
	font-size: 30px;
	letter-spacing: 0.1em;
}

.services-title {
	color: #f7f3ea;
}

.title-line {
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0);
	display: inline-block;
	height: 4px;
	width: 90px;
	border-radius: 2px;
	transform: translateX(-50%);
}
.services-title-line {
	background-color: #14a58c;
}



.general-item {
	display: flex;
	flex-direction: column;
}

.general-img {
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 40px;
}

.general-list {
	display: grid;
	grid-template-columns: repeat(4, 3fr);
	gap: 30px;

	@include tablet {
		grid-template-columns: repeat(2, 3fr);
	}

	@include mobile {
		grid-template-columns: repeat(1, 3fr);
	}
}

