.skills {
	background-color: #fff;
	padding-top: 80px;
	padding-bottom: 60px;
}

.skills-title {
	color: #434e67
}

.skills-text {
	color: #434e67
}

.skills-title-line {
	background-color: #bdbdbd;
}

.skills-subtitle {
	color: #434e67;
}

.statistic {
	position: relative;
	text-align: center;
}
.percent {
	color: #434e67;
	font-weight: 400;
	font-size: 50px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -80%);
}